#monetization {
  padding-bottom: 90px;
}

.monetization__pattern > * + * {
  margin-top: 50px;
}

.monetization-item {
  padding: 40px;
  background: #ffffff;
  border-radius: 48px;

  color: $textColor;

  width: 48%;

  & h4 {
    font-weight: 700;
    font-size: 48px;
    line-height: 1.04;
  }

  & p {
    font-weight: 700;
    font-size: 36px;
    line-height: 1.35;

    max-width: 500px;
  }
}

.monetization-item > * + * {
  margin-top: 25px;
}

.monetization__header {
  color: $secondaryColor;
  margin-bottom: 50px;
}

.monetization__top {
  display: flex;
  justify-content: space-between;
}

.monetization__bottom {
  & .monetization-item {
    width: 100%;

    & p {
      max-width: 960px;
    }
  }
}
